<template>
    <text
            :class="[name]"
            :style="{ fontSize, color }"
    >
    </text>
</template>

<script>
    import {isNumber} from '../../util/util';

    export default {
        name:'SunnyIcon',
        props: {
            /**/
            name: String,
            size: [String, Number],
            color: String
        },

        computed: {
            fontSize() {
                return isNumber(this.size) ? `${this.size}rpx` : this.size;
            }
        }
    };
